:root {
    --mainColor:#EB1537;
    --borderColor:#E4E4E4;
}
*, html, body, h1, h2, h3, h4, h5, h6, div, p, span, a {
    padding: 0;
    margin: 0;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.1px;
}

a {
    color: inherit;
    text-decoration: none;
}

* {
    box-sizing: border-box;
}

.p5{
    padding: 5px;
}
.p10{
    padding: 10px;
}
.button{
    cursor: pointer;
    outline: none;
    border-radius: 5px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
}
.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}

@media(min-width: 576px) {
    .container {
        max-width: 540px;
    }
}

@media(min-width: 768px) {
    .container {
        max-width: 720px;
    }
}

@media(min-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media(min-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}
