.top-card-credit{
    min-height: 600px;
    height: 100%;
    border-radius: 20px;
    background-color: var(--mainColor);
    color: white;
    position: sticky;
    top: 0;
    left: 0;
    box-sizing: border-box;


}
.title{
    font-size: 170%;
    font-weight: 900;
    position: absolute;
    top: 10%;
    left: 10%;
}
.little-title{
    position: absolute;
    font-size: 100%;
    font-weight: 100;
    top: 20%;
    left: 8%;
    text-align: center;
    width: 80%;
}
.top-card-credit-img{
    width: 80%;
    height: auto;
    position: absolute;
    left: 8%;
    top: 25%;
}
.more-details-button{
    margin: 0 auto;
    width: 40%;
    height: 10%;
    padding: 5px;
    position: absolute;
    top: 80%;
    left: 30%;
    border: 1px solid white;
    background-color: transparent;
    color: white;
}