.main{
    position: relative;
    width: 100%;
    height: auto;
}
.top-menu{
    border-bottom: 1px solid var(--borderColor);
    margin-bottom: 20px;
}
.language-ul, .menu-list{
    list-style-type: none;
    width: 100%;
}
.language-ul li, .menu-list li{
    float: left;
    padding: 5px;
    font-size: 14px;
    font-family: 'Inter', sans-serif;
    color: gray;
}
.uzb-li{
    color: var(--mainColor);
}
.menu-list li{
    margin-right: 5px;
}
.language-ul li div{
    border-right: 1px solid var(--borderColor);
    padding-right: 5px;
}

.catalogs{
    margin-left: -20%;
}
.ant-switch{
    background-color: black;
}
.ant-switch-checked {
     background-color: var(--borderColor);
 }

.catalog-button{
    width: 90%;
    margin: 0 auto;
    height: 40px;
    background-color: var(--mainColor);
    color: white;
    padding: 5px;
    border: none;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    outline: none;
}
.catalog-button:hover{
    background-color: rgba(180,0,0,1);
    transition-duration: 1s;
}

.search{
    width: 100%;
    border-radius: 5px;
    border: none;
    position: relative;
    height: 40px;
}
.search-input{
    width: 100%;
    height: 40px;
}
.search-input:focus, .search-input-focused {
    border-color: var(--mainColor);
    border-right-width: 1px !important;
    outline: 0;
    box-shadow: 0 0 0 2px rgba(255, 10, 10, 0.2);
}
.search-input:hover{
    border: 1px solid var(--mainColor);
}
.search-icon{
    background-color: var(--mainColor);
    padding: 10px 20px;
    color: white;
    width: 98%;
    height: 40px;
    border-radius: 0 5px 5px 0;
}
.user-button{
    background-color: white;
    color: rgba(0,0,0,0.8);
    margin-left: 2px;
    padding-left: 5px;
    border-radius: 5px;
    width: 100%;
    font-family: 'HelveticaNeue';
}
.user-button:hover{
   background-color: #f0f2f4;
    color: var(--mainColor);
    transition-duration: 0s;
}
.category-list{
    width: 80%;
    margin: 0 auto;
    margin-top: -15%;

}
.category-list ul{
    list-style-type: none;
    width: 100%;
}
.category-list ul li{
    color: var(--mainColor);
    float: left;
    margin: 10px 10px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
}
.category-list ul li:nth-child(3n){
    color: #0400EB;
}
.category-list ul li:nth-child(4n){
    color: #E8C900;
}
.category-list ul li:hover{
    background-color: #f0f2f4;
}