.main-service{
    display: flex;
    justify-content: center;
}
.service-card{
    width: 100%;
    border-radius: 10px;
    background-color: #3167EB;
    min-height: 400px;
    padding: 30px 20px;
}
.title-service-card{
    font-size: 170%;
    font-weight: 700;
    color: white;
}
.delivery{
    color: white;
    width: 100%;
    /*font-size: 22px;*/
    font-weight: 600;
}
.servises-hover{
    border-radius: 10px;
}
.servises-hover:hover{
    cursor: pointer;
    background-color: #3228CA;
}
.last{
    padding-bottom: 200px;
}