.big-post{
    width: 100%;
    position: relative;
    height: 400px;
    background-color: #F7EAA3;
    border-radius: 20px;
}
.carousel-content{
    position: relative;
    top: 20%;
    left: 10%;
    width: 80%;
}
.slider-item{
    width: 90%;
    height: auto;
}
.buy-button{
    border: 1px solid black;
    background-color: transparent;
    width: 40%;
}
.buy-button:hover{
    background-color: black;
    color: white;
    transition-duration: 1s;
}
.carousel-title{
    font-size: 200%;
    font-weight: 700;
}
.category-title{
    font-weight: 600;
    font-size: 18px;
    width: 400px;
    padding-top: 10px;
}
.category-card{
    overflow: hidden;
    cursor: pointer;
}
.category-card:hover{
    border: 1px solid var(--mainColor);
    box-shadow: 0px 0.01em 1px 1px var(--mainColor);
    transition-duration: 500ms;
}